import { getRandomNumber } from './util.js';
import dayjs from 'dayjs';

const descriptions = [
  'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
  'Cras aliquet varius magna, non porta ligula feugiat eget.',
  'Fusce tristique felis at fermentum pharetra.',
  'Aliquam id orci ut lectus varius viverra.',
  'Nullam nunc ex, convallis sed finibus eget, sollicitudin eget ante.',
  'Phasellus eros mauris, condimentum sed nibh vitae, sodales efficitur ipsum.',
  'Sed blandit, eros vel aliquam faucibus, purus ex euismod diam, eu luctus nunc ante ut dui.',
  'Sed sed nisi sed augue convallis suscipit in sed felis.',
  'Aliquam erat volutpat. Nunc fermentum tortor ac porta dapibus.',
  'In rutrum ac purus sit amet tempus.'
];

const generateDescription = () => {
  const res = [];

  for (let i = 0; i < getRandomNumber(1, 5); i++) {
    res.push(descriptions[getRandomNumber(0, descriptions.length - 1)]);
  }

  return res;
}

const generateNamePoint = () => {
  const pointNames = ['Geneva', 'New York', 'Rome', 'Paris', 'Moscow', 'London', 'Tokyo', 'Sydney'];

  return pointNames[getRandomNumber(0, pointNames.length - 1)];
};

const generatePicturesPoint = () => {
  const pictures = [];

  for (let i = 0; i < getRandomNumber(0, 5); i++) {
    pictures.push({
      src: `http://picsum.photos/248/152?r=${getRandomNumber(1, 10000)}`,
      description: descriptions[getRandomNumber(0, descriptions.length - 1)]
    });
  }

  return pictures;
}

const generateOffersId = () => {
  const offers = [];

  for (let i = 0; i < getRandomNumber(1, 5); i++) {
    offers.push(getRandomNumber(1, 20));
  }

  return offers;
};

const generateOffersByType = () => {
  const type = ['taxi', 'bus', 'train', 'ship', 'drive', 'flight', 'check-in', 'sightseeing', 'restaurant'];

  return type[getRandomNumber(0, type.length - 1)];
};

const generatePoint = () => {
  const generateDate = dayjs()
    .add(getRandomNumber(0, 7), 'day')
    .add(getRandomNumber(0, 5), 'hour')
    .add(getRandomNumber(1, 60), 'minute')
    .format();
  
  return {
    price: getRandomNumber(20, 200),
    dateFrom: generateDate,
    dateTo: dayjs(generateDate)
      .add(getRandomNumber(0, 7), 'day')
      .add(getRandomNumber(0, 5), 'hour')
      .add(getRandomNumber(1, 60), 'minute')
      .format(),
    destination: {
      description: generateDescription(),
      name: generateNamePoint(),
      pictures: generatePicturesPoint()
    },
    id: getRandomNumber(0, 100),
    isFavorite: Boolean(getRandomNumber(0, 1)),
    offers: generateOffersId(),
    type: generateOffersByType()
  };
};

const generateOffers = () => {
  const offers = [];

  for (let i = 1; i < 21; i++) {
    offers.push({
      id: i,
      title: descriptions[getRandomNumber(0, descriptions.length - 1)],
      price: getRandomNumber(30, 120)
    });
  }

  return offers;
};

export { generatePoint, generateOffers };