import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import AbstractView from '../framework/view/abstract-view.js';

dayjs.extend(relativeTime)

const generateOffersTemplate = (ids, offers) => {
  let template = '';

  for (const offer of offers) {
    if (ids.includes(offer.id)) {
      template += `<li class="event__offer">
        <span class="event__offer-title">${offer.title}</span>
        &plus;&euro;&nbsp;
        <span class="event__offer-price">${offer.price}</span>
      </li>`;
    }
  }

  return template;
};

export default class PointView extends AbstractView {
  #point = null;
  #offersObject = null;

  constructor(point, offersObject) {
    super();
    this.#point = point;
    this.#offersObject = offersObject;
  }

  get template() {
    const { price, dateFrom, dateTo, type, destination, isFavorite, offers } = this.#point;

    return `<li class="trip-events__item">
        <div class="event">
          <time class="event__date" datetime=${dayjs(dateFrom).format('YYYY-MM-DD')}>${dayjs(dateFrom).format('MMM DD')}</time>
          <div class="event__type">
            <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
          </div>
          <h3 class="event__title">${type} ${destination.name}</h3>
          <div class="event__schedule">
            <p class="event__time">
              <time class="event__start-time" datetime=${dayjs(dateFrom).format('YYYY-MM-DDTHH:mm')}>${dayjs(dateFrom).format('HH:mm')}</time>
              &mdash;
              <time class="event__end-time" datetime=${dayjs(dateTo).format('YYYY-MM-DDTHH:mm')}>${dayjs(dateTo).format('HH:mm')}</time>
            </p>
            <p class="event__duration">${dayjs(dateTo).from(dayjs(dateFrom), true)}</p>
          </div>
          <p class="event__price">
            &euro;&nbsp;<span class="event__price-value">${price}</span>
          </p>
          <h4 class="visually-hidden">Offers:</h4>
          <ul class="event__selected-offers">
            ${generateOffersTemplate(offers, this.#offersObject)}
          </ul>
          <button class="event__favorite-btn ${isFavorite ? 'event__favorite-btn--active' : ''}" type="button">
            <span class="visually-hidden">Add to favorite</span>
            <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
              <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
            </svg>
          </button>
          <button class="event__rollup-btn" type="button">
            <span class="visually-hidden">Open event</span>
          </button>
        </div>
      </li>`;
  }

  setOpenClickHandler = (callback) => {
    this._callback.click = callback;
    this.element.querySelector('.event__rollup-btn').addEventListener('click', this.#openClickHandler);
  };

  #openClickHandler = (evt) => {
    evt.preventDefault();
    this._callback.click();
  };
}