import PointView from '../view/point-view.js';
import EditPointView from '../view/edit-point-view.js';
import { render, replace } from '../framework/render.js';

export default class PointPresenter {
  #pointData = null;
  #offersData = null;
  #container = null;
  #pointComponent = null;
  #editPointComponent = null;

  constructor(container) {
    this.#container = container;
  }

  init = (pointData, offersData) => {
    this.#pointData = pointData;
    this.#offersData = offersData;
    this.#pointComponent = new PointView(pointData, offersData);
    this.#editPointComponent = new EditPointView(pointData);

    render(this.#pointComponent, this.#container);

    this.#pointComponent.setOpenClickHandler(() => {
      this.#replacePointToForm();
      document.addEventListener('keydown', this.#onEscapeDown);
    });

    this.#editPointComponent.setCloseClickHandler(() => {
      document.removeEventListener('keydown', this.#onEscapeDown);
      this.#replaceFormToPoint();
    });

    this.#editPointComponent.setSubmitHandler(() => {
      document.removeEventListener('keydown', this.#onEscapeDown);
      this.#replaceFormToPoint();
    });
  }

  #replacePointToForm = () => {
    replace(this.#editPointComponent, this.#pointComponent);
  };

  #replaceFormToPoint = () => {
    replace(this.#pointComponent, this.#editPointComponent);
  };

  #onEscapeDown = (evt) => {
    if(evt.key === 'Escape' || evt.key === 'Esc') {
      document.removeEventListener('keydown', onEscapeDown);
      this.#replaceFormToPoint();
    }
  };
}