import SortView from '../view/sort-view.js';
import ListView from '../view/list-view.js';
import AddPointView from '../view/add-point-view.js';
import ListClearView from '../view/list-clear-view.js';
import { render, RenderPosition } from '../framework/render.js';
import PointPresenter from './point-presenter.js';

const pointPresenters = new Map();

export default class ListPresenter {
  #listComponent = new ListView();
  #listContainer = null;
  #pointsModel = null;
  #offersModel = null;

  init = (listContainer, pointsModel, offersModel) => {
    this.#listContainer = listContainer;
    this.#pointsModel = [...pointsModel.points];
    this.#offersModel = offersModel.offers;

    this.#renderSort();
    this.#renderList();
    this.#renderPoints();
    this.#renderAddForm();

    this.#isClearList(this.#listContainer);
  }

  #isClearList = (listContainer) => {
    if (!this.#listComponent.element.children.length) {
      listContainer.replaceChildren();
      render(new ListClearView(), listContainer);
    }
  };

  #renderSort = () => render(new SortView(), this.#listContainer);

  #renderList = () => render(this.#listComponent, this.#listContainer);

  #renderPoint = (pointData, offersData) => {
    const pointPresenter = new PointPresenter(this.#listComponent.element);
    pointPresenters.set(pointData.id, pointPresenter);
    pointPresenter.init(pointData, offersData);
  };

  #renderPoints = () => {
    for (let i = 0; i < this.#pointsModel.length; i++) {
      this.#renderPoint(this.#pointsModel[i], this.#offersModel);
    }
  };

  #renderAddForm = () => render(new AddPointView(this.#pointsModel[0], this.#offersModel), this.#listComponent.element, RenderPosition.AFTERBEGIN);
}